dschmied von Straßburg; 2. ein Dolmetsch, Sampson Pine, ein Jude; 3. zwei Schreiber, Henselin der junge und der von Ohnheim, ein alter. […] Dieses fahrende Volk, dessen Verpflegung manches Stückfaß elsässischen Weines verschlungen haben mag, beeilte sich nicht allzusehr und überreichte erst im Jahre 1336 die vollendete, mit manchem Korrekturstreiflein überklebte, aber als stattlicher Foliant geschriebene Arbeit, von der sie bemerkten: ›und alles daz hie nach geschriben stat, daz ist ouch Parzeval‹ ihrem Schirmherrn, dabei ihn launig über die Kosten tröstend, die sie selber auf 200 Pfund anschlugen. ")]),_vm._m(7),_vm._m(8),_c('p',{staticClass:"MsoNormal"},[_vm._v("Als Indiz dafür, dass es sich bei Hs. V um die »"),_c('span',{staticStyle:{"letter-spacing":"1.0pt"}},[_vm._v("Originalhandschrift")]),_vm._v("« handelt, führt der Herausgeber "),_c('span',{staticStyle:{"font-variant":"small-caps"}},[_vm._v("Karl Schorbach")]),_vm._v(" die Tatsache an, dass der Codex in seiner besonderen, sorgfältig eingerichteten Textgestalt »unter den Augen der Dichter für Ulrich von Rappoltstein hergestellt wurde« und noch aus dem 16. Jahrhundert Einträge enthält, die auf den Familienbesitz verweisen."),_vm._m(9),_vm._v(" Nicht bekannt ist freilich, wie viele Vorstufen bzw. Entwürfe der vorliegenden Fassung vorausgegangen sind. Eine bemerkte und korrigierte Augenabirrung auf Bl. "),_c('router-link',{attrs:{"to":"/faksimile/V/300v"}},[_vm._v("300v")]),_vm._v("b, 5. Zeile von unten (NP 36200/36201; Sch. 839.39/40), weist darauf hin, dass zumindest Teile von Hs. V von einer Vorlage abgeschrieben worden sind und der Codex Donaueschingen 97 eine »Reinschrift« darstellt."),_vm._m(10),_vm._v(" Angesichts dieser Umstände soll der Begriff des ,Originals‘ bzw. der ,Originalhandschrift‘ im Folgenden pragmatisch für den Sachverhalt verwendet werden, dass der Codex mit der darin überlieferten, aus Wolframs ›Parzival‹ und den ›Conte du Graal‹-Fortsetzungen bestehenden Textgestalt von dem Mäzen Ulrich